
import Emoji from '../components/Emoji'
import React from 'react'
import css from 'styled-jsx/css'
import { ALL_EMOTIONS, EMOTION_CONTENT, EMOTION_ANGRY, EMOTION_SAD, EMOTION_HAPPY, EMOTION_SUPRISED } from '../game'

const CSS = css`
.level {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 100%;
    height: 100%;
}

.level .emoji {
    position: absolute;
    font-size: 60px;
}
`

const _DefaultState = {
    emojis: []
}

class SceneLevel extends React.Component {
    constructor() {
        super()

        this.state = _DefaultState
    }

    componentDidMount() {
        this._setLevel(this.props.level)
    }

    componentDidUpdate(prevProps) {
        if (prevProps.level != this.props.level) {
            this._setLevel(this.props.level)
        }
    }

    renderEmoji(e, i) {
        return (
            <Emoji key={i}
                   emotion={e.emotion}
                   dead={e.dead}
                   style={{
            left: e.x + 'vw',
            top: e.y + 'vh',
            }} />
        )
    }

    render () {
        return (
            <div className="level">
                <style global jsx>{CSS}</style>

                {this.state.emojis.map(this.renderEmoji.bind(this))}
            </div>
        )
    }

    _setLevel(levelNo) {
        this._killAllEmojis()
        if (levelNo === 1) {
            this._generateEmojis(10)
        }
        else if (levelNo === 2) {
            this._generateEmojis(20)
        }
    }

    _generateEmojis(count) {
        let emojis = [...this.state.emojis]
        for (var i = 0; i < count; ++i) {
            emojis.push(this._newEmoji())
        }
        this.setState({
            emojis: emojis
        })
    }

    _newEmoji() {
        return {
            emotion: this._randomEmotion(),
            x: -30 + Math.random() * 60,
            y: -30 + Math.random() * 60,
        }
    }

    _killAllEmojis() {
        let emojis = this.state.emojis.map(this._killEmoji.bind(this))

    }

    _killEmoji(emoji) {
        return {
            ...emoji,
            dead: true
        }
    }

    _randomEmotion() {
        return ALL_EMOTIONS[Math.floor(Math.random() * ALL_EMOTIONS.length)]
    }
}

export default SceneLevel


